"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Search, MapPin, Heart } from 'lucide-react'
import { useState, useEffect, useCallback } from "react"
import { useRouter } from 'next/navigation'
import { useCity } from "@/context/city-context"
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
  type CarouselApi,
} from "@/components/ui/carousel"
import useEmblaCarousel from 'embla-carousel-react'
import { cn } from "@/lib/utils"

// Placeholder data for the carousel
const heroBanners = [
  {
    id: 1,
    type: "video",
    title: "Kalki 2898 AD",
    image: "https://assets-in.bmscdn.com/promotions/cms/creatives/1717054668508_kalki2898adweb.jpg",
    videoSrc: "https://assets-in.bmscdn.com/promotions/cms/creatives/1717054668508_kalki2898adweb.mp4", // This is a placeholder structure, I'll use a working sample video
    // Using a reliable sample video for demo purposes as the above URL is hypothetical
    demoVideo: "https://interactive-examples.mdn.mozilla.net/media/cc0-videos/flower.mp4",
    color: "from-amber-900 to-yellow-900"
  },
  {
    id: 2,
    type: "image",
    title: "Oppenheimer",
    image: "https://assets-in.bmscdn.com/promotions/cms/creatives/1690628905918_web.jpg",
    color: "from-orange-900 to-red-900"
  },
  {
    id: 3,
    type: "image",
    title: "Jawan",
    image: "https://assets-in.bmscdn.com/promotions/cms/creatives/1692875161616_jawan1240x300.jpg",
    color: "from-emerald-900 to-teal-900"
  },
  {
    id: 4,
    type: "image",
    title: "Throwback 2025",
    image: "https://assets-in.bmscdn.com/promotions/cms/creatives/1702976873541_throwback2023web.jpg",
    color: "from-purple-900 to-pink-900"
  }
]

export function HeroSection() {
  const [searchQuery, setSearchQuery] = useState("")
  const [selectedCity, setSelectedCity] = useState("Delhi")
  const router = useRouter()
  const { setSelectedCity: setCityContext } = useCity()
  const [api, setApi] = useState<CarouselApi>()
  const [current, setCurrent] = useState(0)

  const cities = ["Delhi", "Mumbai", "Bangalore", "Hyderabad", "Chennai", "Goa", "Gurugram"]

  useEffect(() => {
    if (!api) {
      return
    }

    setCurrent(api.selectedScrollSnap())

    api.on("select", () => {
      setCurrent(api.selectedScrollSnap())
    })
  }, [api])


  const handleSearch = () => {
    if (searchQuery.trim()) {
      setCityContext(selectedCity)
      router.push(`/movies?search=${encodeURIComponent(searchQuery)}`)
    } else {
      setCityContext(selectedCity)
      router.push("/movies")
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSearch()
    }
  }

  return (
    <div className="relative bg-background py-8">
      {/* Carousel Section */}
      <div className="w-full max-w-[1400px] mx-auto px-4 mb-8">
        <Carousel
          setApi={setApi}
          opts={{
            align: "center",
            loop: true,
          }}
          className="w-full relative"
        >
          <CarouselContent className="-ml-4">
            {heroBanners.map((banner, index) => (
              <CarouselItem key={banner.id} className="pl-4 md:basis-1/2 lg:basis-2/3">
                <div className={cn(
                  "relative aspect-[2/1] md:aspect-[21/9] overflow-hidden rounded-xl transition-all duration-300 ease-in-out shadow-lg",
                  current === index ? "scale-100 ring-2 ring-primary/20" : "scale-90 opacity-70 blur-[1px]"
                )}>
                  {/* Fallback pattern/gradient if image fails or while loading (simulated with div) */}
                  <div className={`absolute inset-0 bg-gradient-to-br ${banner.color} flex items-center justify-center`}>
                    {banner.type === 'video' ? (
                      <video
                        src={banner.demoVideo || banner.videoSrc}
                        poster={banner.image}
                        autoPlay
                        muted
                        loop
                        playsInline
                        className="w-full h-full object-cover opacity-80"
                      />
                    ) : (
                      <img
                        src={banner.image}
                        alt={banner.title}
                        className="w-full h-full object-cover mix-blend-overlay opacity-50"
                        onError={(e) => {
                          e.currentTarget.style.display = 'none';
                        }}
                      />
                    )}
                    <div className="absolute inset-0 flex flex-col items-center justify-center text-white px-4 text-center z-10 pointer-events-none">
                      <h2 className="text-3xl md:text-5xl font-bold mb-2 drop-shadow-lg">{banner.title}</h2>
                      <Button className="mt-4 pointer-events-auto" variant="secondary">Book Now</Button>
                    </div>
                  </div>
                </div>
              </CarouselItem>
            ))}
          </CarouselContent>

          <div className="hidden md:block">
            <CarouselPrevious className="left-8 bg-black/50 hover:bg-black/70 text-white border-none h-12 w-12" />
            <CarouselNext className="right-8 bg-black/50 hover:bg-black/70 text-white border-none h-12 w-12" />
          </div>

          {/* Dots Indicator */}
          <div className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 flex space-x-2">
            {heroBanners.map((_, index) => (
              <button
                key={index}
                className={cn(
                  "w-2 h-2 rounded-full transition-all",
                  current === index ? "bg-primary w-6" : "bg-muted-foreground/30"
                )}
                onClick={() => api?.scrollTo(index)}
              />
            ))}
          </div>

        </Carousel>
      </div>

      {/* Search Section Overlay (or positioned below as per new design flow) */}
      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 mt-12">
        <div className="bg-card rounded-2xl shadow-xl border border-border/50 p-6 md:p-8 max-w-4xl mx-auto">
          {/* ... existing search structure ... */}
          <div className="grid md:grid-cols-12 gap-4 items-end">

            {/* Location Selector */}
            <div className="md:col-span-3">
              <label className="block text-sm font-medium text-foreground mb-2">City</label>
              <div className="flex items-center gap-2 px-3 py-2.5 border border-border rounded-lg bg-background hover:border-primary/50 transition-colors">
                <MapPin className="w-4 h-4 text-primary" />
                <select
                  value={selectedCity}
                  onChange={(e) => setSelectedCity(e.target.value)}
                  className="flex-1 bg-transparent text-sm font-medium focus:outline-none cursor-pointer"
                >
                  {cities.map((city) => (
                    <option key={city} value={city}>{city}</option>
                  ))}
                </select>
              </div>
            </div>

            {/* Search Input */}
            <div className="md:col-span-6">
              <label className="block text-sm font-medium text-foreground mb-2">Search</label>
              <div className="flex items-center gap-2 px-3 py-2.5 border border-border rounded-lg bg-background hover:border-primary/50 transition-colors focus-within:ring-1 focus-within:ring-primary">
                <Search className="w-4 h-4 text-muted-foreground" />
                <input
                  type="text"
                  placeholder="Search for Movies, Events, Plays..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  onKeyPress={handleKeyPress}
                  className="flex-1 bg-transparent text-sm font-medium placeholder:text-muted-foreground focus:outline-none"
                />
              </div>
            </div>

            {/* Action Buttons */}
            <div className="md:col-span-3 flex gap-2">
              <Button
                onClick={handleSearch}
                className="flex-1 bg-primary hover:bg-primary/90 font-semibold"
              >
                Search
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
